<template>
  <div style="min-width: 1100px;">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设备列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--<el-row>-->
        <!--<el-button type="primary" @click="addDialogVisible = true">添加</el-button>-->
        <!--<el-button type="success" style="float: right" @click="changeBut">设备统计</el-button>-->
      <!--</el-row>-->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column label="终端ID" prop="deviceName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="终端IP">
          <template slot-scope="scope">
            <span>{{scope.row.deviceIp == null ||  scope.row.deviceIp == '' ? '-' : scope.row.deviceIp}}</span>
          </template>
        </el-table-column>
        <!--<el-table-column label="终端序列号" prop="serialNo" :show-overflow-tooltip="true"></el-table-column>-->
        <el-table-column label="终端状态" prop="workStatus">
          <template slot-scope="scope">
            <span>{{scope.row.workStatus == false ? '离线' : '在线'}}</span>
          </template>
        </el-table-column>
        <!--<el-table-column label="密钥更新频率" prop="qkeyFreq"></el-table-column>-->
        <el-table-column label="密钥使用量" prop="keyNum">
          <template slot-scope="scope">
            <span>{{scope.row.keyNum == null ||  scope.row.keyNum == '' ? '-' : scope.row.keyNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="加密速率" prop="encRate">
          <template slot-scope="scope">
            <span>{{scope.row.encRate == null ||  scope.row.encRate == '' ? '-' : scope.row.encRate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="解密速率" prop="decRate">
          <template slot-scope="scope">
            <span>{{scope.row.decRate == null ||  scope.row.decRate == '' ? '-' : scope.row.decRate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="在线时间" prop="workTime" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.workTime == null ||  scope.row.workTime == '' ? '-' : scope.row.workTime}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="warning" size="mini" @click="rebootMethod(scope.row.deviceName)">重启</el-button>
            <el-button type="danger" size="mini"  @click="zeroMethod(scope.row.deviceName)">置零</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog title="添加" :visible.sync="addDialogVisible" width="500px" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef"
               label-width="100px">
        <el-form-item label="终端ID" prop="qdeviceId">
          <el-input v-model.trim="addForm.qdeviceId"></el-input>
        </el-form-item>
        <el-form-item label="终端序列号" prop="serialNo">
          <el-input v-model.trim="addForm.serialNo"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" v-preventReClick="1000" @click="submitAdd">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {

      return {
        userlist: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
        addDialogVisible: false,
        addForm: {
          qdeviceId: '',
          serialNo: '',
        },
        addFormRules: {
          qdeviceId: [{
            required: true,
            validator: this.Global.valiNumber,
            trigger: 'blur'
          }],
          serialNo: [
            {  required: true,
              validator: this.Global.valiSerialNo,
              trigger: 'blur' }
          ],
        },
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`status/listDeviceInfo/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取用户信息失败！');
        this.userlist = res.data.list;
        this.total = res.data.total;
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getUserList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getUserList()
      },
      addDialogClosed() {
        this.$refs.addFormRef.resetFields();
      },
      submitAdd() {
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post('status/addQDevice', {
            'qdeviceId': this.addForm.qdeviceId,
            'serialNo': this.addForm.serialNo
          });
          if (res.code !== 0) return this.$message.error('添加失败!');
          this.$message.success('添加成功!');
          this.addDialogVisible = false;
          this.getUserList()
        })
      },
      async deleteByName(id) {
        const confirmResult = await this.$confirm(
          '确定要删除？', '提示', {
            confirmButtonText: '确定',
            cancelButtontext: '取消',
            type: 'warning',
          }
        ).catch(err => err);
        if (confirmResult !== 'confirm') {
          return
        }
        const {data: res} = await this.$http.post(`status/delQDevice?id=${id}`);
        if (res.code !== 0) return this.$message.error('删除失败！');
        this.$message.success('删除成功！');
        this.getUserList()
      },
      changeBut(){
        this.$router.push('/total')
      },
      async rebootMethod(name) {//1
        const confirmResult = await this.$confirm(
          `此操作将重启设备：${name}，是否继续？`, '提示', {
            confirmButtonText: '确定',
            cancelButtontext: '取消',
            type: 'warning',
          }
        ).catch(err => err);
        if (confirmResult !== 'confirm') return;
        const {data: res} = await this.$http.post('qems/operation', {
          'deviceName':name,
          'operation':1
        });
        if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '重启失败！');
        this.$message.success('重启成功！');
      },
      async zeroMethod(name) {//2
        const confirmResult = await this.$confirm(
          `此操作将置零设备：${name}，是否继续？`, '提示', {
            confirmButtonText: '确定',
            cancelButtontext: '取消',
            type: 'warning',
          }
        ).catch(err => err);
        if (confirmResult !== 'confirm') return;
        const {data: res} = await this.$http.post('qems/operation', {
          'deviceName':name,
          'operation':2
        });
        if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '置零失败！');
        this.$message.success('置零成功！');
      //  特步：女卫衣69+129,板鞋69+男卫衣79；卫生巾64.9+安踏：男鞋144+79+女裤子114+男板鞋104；29+168+34+40+34.8+336.86
      }
    }
  }
</script>
<style lang="less" scoped>
  .el-select {
    width: 100%;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 120px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .el-form--inline .el-form-item__content > span {
    display: inline-block;
    max-width: 500px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>
